import isHidden from './isHidden'

const openHelpLink = () =>
  window
    .open(
      `https://github.com/epam/ketcher/blob/${process.env.HELP_LINK}/documentation/help.md#ketcher-overview`
    )
    ?.focus()

export default {
  help: {
    shortcut: ['?', '&', 'Shift+/'],
    action: () => openHelpLink(),
    hidden: (options) => isHidden(options, 'help')
  }
}
