import { range } from 'lodash/fp'

const PERIODIC_TABLE_COLUMNS = 19

function Header() {
  return (
    <tbody>
      <tr>
        {range(0, PERIODIC_TABLE_COLUMNS).map((index) => (
          <th key={index}>{index || ''}</th>
        ))}
      </tr>
    </tbody>
  )
}

export default Header
